.range__picker {
  position: absolute;
  bottom: -100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-sizing: border-box;
  width: 100%;
  height: 54px;
  padding: 10px 10px 8px 10px;
  background-color: #fff;
  border-radius: 8px;

  &--text {
    display: flex;
    gap: 73px;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 15px;
    margin: 0;

    position: relative;

    & :nth-child(n + 1) {
      margin: 0;
      white-space: nowrap;

      &::before {
        position: absolute;
        display: block;
        content: '';
        width: 28px;
        bottom: 0;
        height: 1px;
        background-color: #b4b4b4;
        right: 0;
      }
    }
  }
}